body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Button-Nice {
  padding: 10px;
  background: rgba(204, 187, 204, 0.4);
  border: 1px solid #997a99;
  border-radius: 50%;
  color: #fff;
  width: 100px;
  height: 100px;
}

.Button-Nice:focus,
.Button-Nice:active {
  box-shadow: 0 0 3pt 2pt #997a99;
  outline: none;
}
.Controls {
  z-index: 1;
  display: flex;
}

.Controls-item {
  margin: 16px;
}

.Controls-item:first-child {
  margin-left: 0;
}

.Controls-item:last-child {
  margin-right: 0;
}
.Kale-Wrapper {
  position: fixed;
  top: 0;
  left: 50%;
}

.Kale-Pop {
  -webkit-animation: KalePop infinite 2s linear;
          animation: KalePop infinite 2s linear;
  height: 40vmin;
}

@-webkit-keyframes KalePop {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }

  18.75%, 43.75%, 68.75%, 93.75% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes KalePop {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }

  18.75%, 43.75%, 68.75%, 93.75% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.Garlic-Wrapper {
  position: fixed;
  top: 37%;
  left: 5%;
}

.Garlic-Salsa {
  height: 40vmin;
}
.Mint-Wrapper {
  position: absolute;
  top: 0;
  right: 0;
}

.Mint-Salsa {
  -webkit-animation: DillSway infinite 4s linear;
          animation: DillSway infinite 4s linear;
}

@-webkit-keyframes DillSway {
  0% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
  25% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  50% {-webkit-transform: rotate(20deg);transform: rotate(20deg);}
  75% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  100% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
}

@keyframes DillSway {
  0% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
  25% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  50% {-webkit-transform: rotate(20deg);transform: rotate(20deg);}
  75% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  100% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.App-header {
  background-color: #607d8b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-title {
  font-size: 5em;
  font-weight: 100;
  margin: 0;
}

.App-link {
  color: #61dafb;
}

.Vegetable-Band {
  position: relative;
  width: 100%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
